import { Swiper, SwiperSlide } from "swiper/react";
import NewsSwiperItem from "./NewsSwiperItem";
import "swiper/css/pagination";
import "swiper/css";
import "./HeroSwiper.css";
import event01 from "../../assets/img/event01.jpg";
import event02 from "../../assets/img/event02.jpg";

export default function NewsSwiper() {
  return (
    <main className="cursor-grab">
      <Swiper slidesPerView={1.2} spaceBetween={100}>
        <SwiperSlide>
          <NewsSwiperItem
            image={event01}
            title={""}
            content={"STEAM"}
            comp1={""}
            comp2={""}
            compTitle={"STEAM"}
            url={""}
          />
        </SwiperSlide>
        <SwiperSlide>
          <NewsSwiperItem
            image={event02}
            title={""}
            content={"202310202310202312"}
            comp1={""}
            comp2={""}
            compTitle={""}
            url={""}
          />
        </SwiperSlide>
      </Swiper>
    </main>
  );
}
